button {
	display: block;
	position: relative;
	padding: 0.25em 0.75em;
	text-transform: uppercase;

	&:before {
		@mixin abs-full-size;
		content: '';
		border: 1px solid currentColor;
		transform: skew(-7deg);
		background: white;
		z-index: -1;
	}
}

button.primary {
	&:after {
		@mixin abs-full-size;
		content: '';
		border: 1px solid currentColor;
		transform: translate(0.3em, 0.3em) skew(-7deg);
		background: white;
		z-index: -2;

	}
}
